import ModuleType from "jx/core/presets/GameModule";
import JXEditor from "jx/editor/Editor";
let plugs = [
    {
        type: "package",
        itemsType: "class",
        path: "jx/editor/editionitems",
        preload: true,
        packageID: "editionitems",
        itemsID: [
            "Common", "Group", "GabaritObject", "GabaritModel", "GabaritMap", "DataObject", "DataGroup", "DataCtrl", "DataModel",
            "Container", "CatalogItem", "CatalogTestItem", "ClassFile", "Todo", "Separator",
            "ScreenData", "InformationsData", "JXEngineInformation", "ProjectInformations", "ProjectInformation", "EditorInformation",
            "FileItem", "FlashtmlItem", "FlashtmlPackItem", "FlashtmlResource", "FLAItem", "FontItem", "FontSelector", "VideoItem", "ImageItem", "SoundItem", "FlaLibItem", "ImageSequenceItem", "Text", "PSD", "PDF", "SpriteSheetItem",
            "BooleanItem", "Rectangle", "Point", "DateItem", "Color", "Parameter", "NumberItem",
            "SimpleText", "TextStyle", "TextStyle2",
            "ComboBox", "SimpleComboBox", "SearchBox", "ListItem", "Button", "ButtonItem",
            "StatusItem", "DropFilesZone",
            "Sequence01", "Quiz", "QuizManagerItem", "QuizThemeItem", "QuizDataItem", "Clip", "Menu",
            "MemoryGameConfig"
        ]
    },
    {
        "id": "Number2", "type": "class", "preload": true,
        "src": "jx/editor/compitemsedit/Number"
    },
    {
        "id": "Number3", "type": "class", "preload": true,
        "src": "jx/editor/compitemsreadonly/Number"
    },
    {
        "id": "Text2", "type": "class", "preload": true,
        "src": "jx/editor/compitemsreadonly/Text"
    },
    {
        "id": "SimpleText2", "type": "class", "preload": true,
        "src": "jx/editor/compitemsreadonly/SimpleText"
    },
    {
        "id": "Daemon", "type": "class", "preload": true,
        "src": "jx/core/utils/Daemon"
    }
];
export default class EditorPreset extends ModuleType {
    constructor(dataObject) {
        dataObject = Object.assign({
            plugs: [],
        }, dataObject);
        dataObject.plugs = dataObject.plugs.concat(plugs);
        super(dataObject);
        // console.log("EditorPreset", this.dataObject)
    }
    initialise(callback) {
        this.jx.editor = new JXEditor(this.jx);
        this.jx.editor.initialise(evt => super.initialise(callback));
        return this;
    }
}
